<template>
  <div id="app">
    <router-view :list = "postList" class = "app" :from="$route.query.from" :navInfo="navInfo"> </router-view>
  </div>
</template>

<script>
  const allPost = require('./components/posts/convert/all.json');
  var navInfo
  var backupNavinfo = require('./components/nav/nav.json')
  var localNavInfo = window.localStorage.navInfo
  if (!localNavInfo) {
    navInfo = backupNavinfo
  } else {
    try {
      navInfo = JSON.parse(localNavInfo)
    } catch (e) {
      navInfo = backupNavinfo
    } finally {
    }
  }
  var postList = {}
  for (var post in allPost) {
    var column = allPost[post].column
    var p = allPost[post]
    // console.log(column);
    if (!postList[column]) {
      postList[column]=[p]
    }else {
      postList[column].push(p)
    }
  }
  export default {
    name: 'app',
    data(){
      return {
        postList:postList,
        navInfo:navInfo
      }
    }
  }
</script>

<style>
html,body{
  position: relative;
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  text-decoration: none;
  /*background: #838383;*/
  position: absolute;
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  background-image: url(./assets/bg.jpg);
  background-size: 100% 100%;
  background-position: center;
  /*background-repeat: round;*/
}
/*markdown style below*/
pre{
  margin: 0.5rem;
  background: #AAAAAA;
  padding: 1.5rem;
  border-radius: 0.2rem;
  text-align: left;
  font-size: 1rem;
  /*box-shadow: 2px 2px 5px;*/
}
code{
  font-size: 0.9rem;
}
h2,h3,h4,h5,h6{
  text-align: left;
  text-indent: 1rem;
  margin: 0;
  padding: 0;
  border: none;
}
</style>



