<template lang="html">
  <div class="navcolumn" v-if="!empty">
    <div class="columnName">{{column}}</div>
    <div class="columnContent" v-for="(site,key) in content" v-if="site&&site.addr&&site.name">
      <button :value="'http://'+site.addr" :addr="site.addr" class="navLink" v-if="!modify" @click="handleNav">{{site.name}}<img :src="'http://'+site.addr+'/favicon.ico'" class="siteIcon"></button>
      <div class="modifyDiv" v-if="modify">
        <div v-if="modify&&!modifySite.on" class="modifyDetail">
          <i class="fa fa-close" @click="del(window.event,column,key)"></i>
          &nbsp;&nbsp;{{site.name}}&nbsp;&nbsp;
          <i class="fa fa-pencil" @click="handleModify(window.event,column,key)"></i>
        </div>
      </div>
    </div>
    <div class="cf"></div>
    <div class="popupModify" v-if="modifySite.on">
      <input v-model = "navInfo[modifySite.column][modifySite.key].name" class ="popupInner">
      <input v-model = "navInfo[modifySite.column][modifySite.key].addr" class ="popupInner">
      <i class = "fa fa-check half" @click="handleModify(window.event,modifySite.column,modifySite.key)"> </i>
      <i class = "fa fa-close half" @click="cancleModify(window.event,modifySite.column,modifySite.key)"> </i>
    </div>
  </div>
</template>

<script>
export default {
  props:["column","content","modify","navInfo"],
  data:function(){
    var empty = true
    this.navInfo[this.column].forEach(c=>{
      if (!!c) {
        empty=false
      };
    });
    return {
      window:window,
      empty:empty,
      modifySite:{
        on:false,
        name:'',
        addr:'',
        column:'',
        key:''}
    }
  },
  methods:{
    handleNav:function(e){
      window.open(e.target.value)
    },
    handleInput:function(e){
      var name = e.path[1].children[0].value
      var addr = e.path[1].children[1].value
    },
    del:function(e,c,k){
      this.navInfo[c].splice(k,1)
    },
    handleModify:function(e,column,key){
      // console.log(column,key);
      this.modifySite.on=!this.modifySite.on
      var target = this.navInfo[column][key]
      this.modifySite.addr=target.addr
      this.modifySite.name=target.name
      this.modifySite.column=column
      this.modifySite.key=key
    },
    cancleModify:function(e,column,key){
      this.navInfo[column][key].name = this.modifySite.name
      this.navInfo[column][key].addr = this.modifySite.addr
      this.modifySite.on=!this.modifySite.on
    },
  },
}
</script>

<style lang="less" scoped>
.cf{
  clear: both;
}

.navcolumn{
  font-family: weiruanyahei;
  align-items: center;
  background:transparent;
  background-color: rgba(199, 199, 199, 0.24);
  justify-content: flex-start;
  flex-wrap: wrap;
  margin: 10px;
  .columnName{
    text-align: left;
    padding-left: 20px;
    width: 100%;
    background:transparent;
    background-color: rgba(255, 255, 255, 0.25);
    font-size: 1.4rem;
    text-transform: capitalize;
    padding: 5px;
  }
  .columnContent{
    width: 23%;
    padding: 1%;
    display: inline-block;
    float: left;
    position: relative;
    overflow: hidden;
    .navLink{
      display: inline-flex;
      justify-content: center;
      align-items: center;
      font-size: 1.2rem;
      color: #FFF;
      text-decoration: none;
      border: 1px solid #FFF;
      padding: 10px 0;
      border-radius: 10px;
      background: transparent;
      outline: none;
      min-width: 6rem;
      width: 100%;
      .siteIcon{
        width: 1.5rem;
        height: 1.5rem;
        margin-left: 0.5rem;
      }
    }
    .navLink:hover{
      transition: all 0.2s;
      background: rgba(179, 179, 179, 0.52);
      transform: scale(1.05);
    }
    .modifyDiv{
      float: left;
      color: #FFF;
      width: 100%;
      .modifyDetail{
        display: flex;
        border: 1px solid #FFF;
        border-radius: 10px;
        padding:10px;
        align-items: center;
        justify-content: center;
      }
      .modifyDetail:hover{
        /*margin: 1%;*/
        border: 1px solid #FFF;
        border-style: dashed;
      }
    }
  }
  .popupModify{
    width: 50%;
    border: 1px solid #FFF;
    padding: 0.5rem;
    display: flex;
    flex-wrap: wrap;
    box-sizing: border-box;
    justify-content: space-around;
    .popupInner{
      width: 100%;
      border-radius: 5px;
      display: block;
      text-align: center;
      height: 2rem;
      margin: 2px;
      border: 1px solid #FFF;
      border-radius: 5px;
      color: #FFF;
      background:transparent;
    }
    .half{
      height: 1rem;
      padding: 0;
      margin: 0;
      display: inline-block;
    }
  }
  
}



</style>



