<template lang="html">
  <div class="gameMain">
    <cell :cellWidth="'33%'" :title="'Title'"></cell>
    <div class="test" @click="test">
      {{life.cur}} / {{life.max}}
    </div>
  </div>
</template>

<script>
const cell = require('./Cell.vue');
var fat = require('./game/state');
console.log(fat);
export default {
  components:{cell:cell},
  data:function(){
    return{
      progressWidth:'50%',
      widthNum:10,
      cellWidth:"50%",
      life:fat.state.life
    }
  },
  methods:{
    test:function(){
      this.life.cur-=1
    }
  },
  mounted:function(){
    var go = setInterval(function(){
      fat.state.life.cur--
      if (fat.state.life.cur<=5) {
        clearInterval(go)
      }
    },100)
  }
}
</script>

<style lang="less">
.gameMain{
  display: flex;
  justify-content: center;
  align-items: center;
  *{
    margin: 0;
    padding: 0;
  }
  
}
</style>



