<template>
<div class="start">

  <clock></clock>
  <div v-bind:class="className" id = "allInOne">
    <input type="text" class="searchColumnCommon" id="searchBar"v-model="search" @input="checkSearch" @keypress="handleEnter">
    <button type="button" name="button" id = "searchBtn"class="searchColumnCommon" @click="handleSearch" v-html="searchBtnIcon"></button>
  </div>
  <div class="result">
    <blog :list = "list" :search="search"></blog>
    <div class="ani" v-if="search=='help'">
      <form class="uploadImg" action="" method="post">
        <input type="file" name="img" @change="upload">
        <img :src="imgstr" alt="uploadimg" id="showImg">
        <div class="bg" id="bg">

        </div>
      </form>

    </div>
    <navmain :navInfo="navInfo" v-if="search=='nav'"></navmain>
    <div class="setting" v-if="search=='setting'">
      setting
    </div>
  </div>
</div>
</template>

<script>
 var icon={
   search:"<i class='fa-search fa fa-fw'>",
   setting:"<i class='fa fa-cog fa-fw'></i>",
   settingSpin:"<i class='fa fa-cog fa-fw fa-spin'></i>",
   blog:"<i class='fa fa-book fa-fw '></i>",
   help:"<i class='fa fa-question fa-fw '></i>",
   nav:"<i class='fa fa-paper-plane fa-fw '></i>",
 }
 import blog from './Blog.vue'
 import navmain from './NavMain.vue'
 import wb from './WB.vue'
 import clock from './count.vue'
 var getFromPost = '' //return from blog menu
 var initState ="state1"
 var firstPage = localStorage.firstPage
 export default {
  name: 'hello',
  props: ['list','from','navInfo','info'],
  components:{blog:blog,navmain:navmain,clock:clock},
  data() {
    return {
      result: "",
      search: this.from||getFromPost||firstPage||'help',
      className: initState,
      hoverClass:'blogcolumn',
      hovering:true,
      searchBtnIcon:icon.search,
      focusData:['nav','blog','help','setting'],
      imgstr:'',
    }
  },
  methods: {
    upload(e){
      var file =  e.target.files[0]
      if (!file.type.indexOf('image')==0) {
        throw new Error('Wrong File Type')
        // console.log('wrong file type');
      }else {
        // fileReader
        var reader = new FileReader()
        reader.readAsDataURL(file);
        // console.log(typeof reader.result);
        reader.onload = function(){
          window.localStorage.uploadImg = reader.result
          document.getElementById('app').style.backgroundImage="url("+reader.result+')'
          // document.getElementById('showImg').src = window.localStorage.uploadImg
        }
        // console.log(new FileReader());
      }
      // console.log(e.target.files);
      // console.log(this.files);
      // console.log(1);
    },
    checkSearch: function() {
      var t = this.search;
      var keywds = this.focusData;
      var check = 0
      for (let w of keywds) {
        if (t===w) {
          check=t
          break
        }
      }
      if (check) {
        this.className='state2'
        switch (check) {
          case 'blog':
            this.searchBtnIcon=icon.blog
            break;
          case 'nav':
            this.searchBtnIcon=icon.nav
            break;
          case 'help':
           this.searchBtnIcon=icon.help
          default:
          break
        }
      }else {
        this.className='state1'
        this.searchBtnIcon=icon.search
      }
    },
    handleSearch: function(e,kw=this.search) {
      if (kw.indexOf('www.') >= 0 || kw.indexOf('.com') >= 0) {
        if (!kw.indexOf('://')>=0) {
          window.open('http://'+kw)
        }
      }else{
        window.open('http://www.baidu.com/baidu?wd='+kw)
      }
    },
    handleEnter:function(e){
      if (this.search&&e.key==='Enter') {
          this.handleSearch(null,this.search)
      }
    }
  },
  beforeRouteEnter(to,from,next){
    if (from.name==='post') {
      getFromPost='blog'
      initState="state2"
    }
    next()
  },
  mounted(){
    // console.log(localStorage.uploadImg);
    // document.getElementById('showImg').src = localStorage.uploadImg
    document.getElementById('app').style.backgroundImage="url("+localStorage.uploadImg+')'
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

<style scoped>

@media screen and (max-width: 1100px) {
  .nav{width: 100%;}
}
.start {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
}
.state1{
  transition: all 1s;
  margin: 0 auto;
  margin-top: 150px;
  margin-bottom: 20px;
}
.bg{
  /*background: #209CF9;*/
  width: 100px;
  height: 100px;
}
.state2{
  transition: all 1s;
  margin: 0 auto;
  margin-top: 20px;
  margin-bottom: 150px;
}
.searchColumnCommon{
  float: left;
  background: transparent;
  background-color: rgba(255, 255, 255, 0.55);
  border: none;
  box-sizing: border-box;
  margin: 0;
  padding: 0;

  height: 2.2rem
}
#searchBar{
  border-right: 2px solid #FFFFFF!important;
  text-align: center;
  width: 30rem;
  font-size: 1.5rem
}
#searchBtn{
  width: 2rem;
  text-align: center;
  border-top-right-radius: 50%;
  border-bottom-right-radius: 50%;
  font-size: 1rem;
}

.slogan{
  width: 80%;
  margin-left: 10%;
  margin-top: 20px;
  font-size: 3rem;
  color: #FFFFFF
}
.result{
  border-radius: 15px;
  display: flex;
  /*flex-direction: column;*/
  justify-content: center;
  width: 80%;
  /*margin: 0 auto;*/
  margin-left: 10%;
}

</style>



