<template lang="html">
  <div class="cell" :style="{width:cellWidth||0}">
    <div class="left">
      {{title}}
    </div>
    <div class="right">
      <div class="slot" @click="handleWidthChange">
        <div  :style="{width:progressWidth+'%'}" class = "progress" id = "progress" >
        </div>
        <div class="background">
        </div>
      </div>
      <div class="text">
        {{progressWidth}}
      </div>
    </div>
    <div class="after">
      1
    </div>
  </div>
</template>

<script>
export default {
  props:['cellWidth','title'],
  data:function(){
    return{
      progressWidth:10
    }
  },
  methods:{
    handleWidthChange(v){
      for (var i = 0; i < 1000; i++) {
        var pw =this.progressWidth;
        var that = this
        var res = (function(index){
          var itv = setTimeout(
            function(){
              that.progressWidth++;
              if (that.progressWidth>100) {
                that.progressWidth=1
                }
              }
              ,i*20)
        })(i)        
      }
  },
  }
}
</script>

<style lang="less">
@mainColor:#656565;
@secondColor:#FBC55D;
@textBackground:rgba(150, 150, 150, 0.52);
.cell{
  display: flex;
  justify-content: flex-start;
  *{
    color: #FFF;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .left{
    background: @mainColor;
    width: 120px;
    border-top-left-radius: 30px;
    border-bottom-left-radius: 30px;
    margin-right: 5px;
  }
  .right{
    width: 100%;
    flex-direction: column;
    div{
      width: 100%;
    }
    .slot{
      height: 66.333%!important;
      position: relative;
      *{
        position: absolute;
        height: 100%;
        left: 0;
        bottom: 5px;
      }
      .background{
        background: @mainColor;
        width: 100%;
        z-index:1;
        }
      .progress{
        background: @secondColor;
        z-index: 2;
        // transition: width 0.5s;
       }
    }
    .text{
      color: #000;
      height: 33.333%;
      background: @textBackground;
    }
  }
  .after{
    display: flex;
    height: 60px;
    width: 120px;
    margin-left: 5px;
    border-top-right-radius: 30px;
    border-bottom-right-radius: 30px;
    background: @mainColor
  }
}
</style>



