var fat ={}
class State {
  constructor(max, cur) {
    this._max = max;
    this._cur = cur;
    if (max<cur) {
      this._cur = max
    }
  }
  get max() {
    return this._max
  }
  get cur() {
    return this._cur
  }
  set cur(v) {
    if (this._max < v) {
      this._cur = this._max
    } else if (v < 0) {
      this._cur = 0
    } else {
      this._cur = v
    }
  }
  set max(v) {
    this._max = v
    // console.log('set max');
  }
}

var life = new State(100,100)
var hunger  =  new State(100,100)
var water =new State(100,100)

fat.state={
  life:life,
  hunger:hunger,
  water:water,
  weight:75,
}
fat.props={
  int:5,
  sta:5,
  dig:10,  
}

Object.defineProperty(fat,'product',{
  get:function(){
    return this.props.sta*5+this.props.dig*10
  },
})
module.exports = fat;



