<template lang="html">
  <div>
    <mu-list-item title="WB title"  :describeText ="content" titleClass = "titleClass" describeTextClass = "describeTextClass" >
      <mu-icon-menu slot="right" :icon="icon" iconClass="iconClass" :anchorOrigin="anchorOrigin" :targetOrigin="targetOrigin" menuClass="menuClass" autoWidth='true' @open="open" @close="close" @itemClick="handlleClick">
        <mu-menu-item title="收藏" leftIcon='star_border'/>
        <mu-menu-item title="取消关注" leftIcon='check' />
        <mu-menu-item title="举报" leftIcon='warning'/>
      </mu-icon-menu>
      <mu-avatar slot="leftAvatar" icon="face"/>
    </mu-list-item>
  </div>
</template>

<script>
export default {
  // props:['avanta','user','content','pic']
  data:function(){
    return {
      content:"XX分钟前 来自XXX",
      anchorOrigin:{
        vertical: 'bottom',horizontal: 'right'
      },
      targetOrigin:{vertical: 'top',horizontal: 'left'},
      icon:"expand_more",
    }
  },
  methods:{
    handlleClick(e){
      console.log('Item Click');
      console.log(e.title + 'was clicked');
    },
    open(){
      this.icon="expand_less"
      console.log('open');
    },
    close(){
      this.icon="expand_more"
      console.log('close');
    }
  }
}
</script>

<style lang="css">
.titleClass{
  text-align: left;
}
.cf{
  clear: both;
}
.describeTextClass{
  text-align: left;
}
.iconClass{
  color: white
}
.menuClass{
  box-shadow: 1px 1px 45px
  /*background: #16C928;*/
  /*width: 100px;*/
}
</style>



