<template lang="html">
  <transition name = 'slide'>
    <div class="wb">
      <mu-appbar title="Weibo" class='header' id="header">
        <mu-icon-button icon="menu" slot="left" @click = "toggle(true)"/>
         <mu-flat-button :label="lable" ref="button" @click="togglePop" slot="left" />
         <mu-popover :trigger="triggerPop" :open="openPop" @close="handleClose">
           <mu-menu @itemClick="handleChangeColumn" >
             <mu-menu-item :title="title" v-for = "title in columns" :disabled="title=='分组'" class = 'group' />
           </mu-menu>
         </mu-popover>
        <mu-icon-button icon="search" @click="goSearch" slot="right"/>
      </mu-appbar>
      <wbdrawer :open="open" :docked="docked" :toggle="toggle"></wbdrawer>
      <div class="demo-infinite-container" v-if="activeTab=='tab1'" id ="ifs">
        <div class="more">
          Show More
        </div>
        <mu-list>
          <div class="column" v-for="item in list">
            <wbheader></wbheader>
            <wbcontent></wbcontent>
          </div>
        </mu-list>
        <mu-infinite-scroll :scroller="scroller" :loading="loading" @load="loadMore"/>
        <mu-icon-button id="addNewBtn" icon="add" class="addNew btnShow" iconClass="addNewIcon"/>
      </div>
      <mu-tabs :value="activeTab" @change="handleTabChange" class = 'footer' id='footer'>
        <mu-tab value="tab1" icon="home"/>
        <mu-tab value="tab2" icon="public"/>
        <mu-tab value="tab3" icon="notifications_none"/>
      </mu-tabs>
    </div>
  </transition>
</template>

<script>
const wbheader = require('./WB_Main.vue');
const wbcontent = require('./WB_Content.vue');
const wbdrawer = require('./WB_Drawer.vue');
export default {
  methods:{
    goSearch:function(){
      this.$router.push('/wb/search')
    },
    toggle (flag) {
      this.open = !this.open
      this.docked = !flag
    },
    handleChange (value) {
     this.value = value
    },
    handleClose (e){
       this.openPop=false
     },
    handleChangeColumn (e){
      this.lable = e.title
    },
    togglePop (e){
     this.openPop =!this.openPop
    },
    loadMore (){
      var loading = this.loading = true
      var that = this
      setTimeout(function(){
        for (var i = 0; i < 10; i++) {
          that.list.push(i)
        }
        that.loading = false
      },1000)
    },
    handleTabChange (val) {
      this.activeTab = val
    },
  },
  data:function(){
    var list =[]
    for (var i = 0; i < 10; i++) {
      list.push('item'+i)
    }
    return {
      open:false,
      docked:true,
      triggerPop:null,
      openPop:false,
      lable:"全部",
      disable:true,
      columns:['全部','我的收藏','我的赞','分组','原创微博','好友圈','特别关注','编辑分组'],
      list,
      loading:false,
      scroller:null,
      num:10,
      activeTab:'tab1',
    }
  },
  mounted () {
    var ifs = document.getElementById('ifs')
    this.triggerPop = this.$refs.button.$el
    this.scroller = ifs
    var head = document.getElementById('header')
    var foot =document.getElementById('footer')
    var addNew = document.getElementById('addNewBtn')
    var ifsToTop = ifs.scrollTop
    ifs.onscroll=function(e){
        var rollDown = e.target.scrollTop>ifsToTop
        ifsToTop = ifs.scrollTop
        //向下滚动则收起
        //否则放下
        if (rollDown&&!head.classList.contains('headerFold')&&ifsToTop>110) {
          head.classList.add('headerFold')
          foot.classList.add('footerFold')
          addNewBtn.classList.remove('btnShow')
          console.log('fold');
        }else if (!rollDown&&head.classList.contains('headerFold')) {
          head.classList.remove('headerFold')
          foot.classList.remove('footerFold')
          addNewBtn.classList.add('btnShow')
          console.log('unfold');
        }
    }
  },
  components:{wbheader:wbheader,wbcontent:wbcontent,wbdrawer:wbdrawer}
}
</script>

<style lang="css">
.draw{
  background: #F42D67;
}
.wb{
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #C7C7C7;
  overflow: hidden;
}
.slide-enter-active, .fade-leave-active {
  transition: opacity .5s
}
.slide-enter, .fade-leave-active {
  opacity: 0
}
.group:last-child{
  border-top: 1px solid #ACACAC
}
.demo-infinite-container{
  height: 100%;
  padding-top: 110px;
  position: absolute;
  width: 100%;
  left: 0;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  overflow-y: scroll;
  /*margin-bottom: 5px;*/
}
::-webkit-scrollbar {
        width: 0px;
    }
.footer{
  transition: bottom 0.5s;
  position: absolute;
  bottom: 0;
  left: 0;
  height: 110px;
}
.header{
  transition: top 0.5s;
  position: absolute;
  top: 0;
  left: 0;
  height: 110px;
  box-shadow: 0px 5px 15px;
  color:#000;
}
.headerFold{
  top: -110px;
}
.footerFold{
  bottom: -110px;
}
.column{
  padding: 0 15px;
  margin: 10px 0;
  background: #FFF
}
.wbContent{text-align: left;}
.more{
  transition: all 1s;
  display: none;
  align-items: center;
  justify-content: center;
  width: 210px;
  left: 50%;
  top: 150px;
  margin-left: -105px;
  border-radius: 25px;
  height: 55px;
  position: fixed;
  background: #00FFFF;
}
.addNew{
  position: fixed;
  right: 30px;
  bottom: 140px;
  background: #EC2B09;
  border-radius: 100%;
  height: 110px;
  width: 110px;
  transform: scale(0);
  opacity: 0;
  transition: all 0.5s;
}
.btnShow{
  transform: scale(1);
  opacity: 1
}
.addNewIcon{

  color:white;
  font-size: 40px
}
</style>



