<template lang="html">
  <div class="count">
    <div class="numContainer" v-if="key<5" v-for="(num,key) in timeText">
      <span class="num">
        {{num}}
      </span>
    </div>
    <div class="numContainer">
      <span class="second" :style="{top:topNum[0]}">
        <span v-for="i in 10" class="num" >{{i-1}}</span>
      </span>
    </div>
  </div>
</template>

<script>
export default {
  data:function(){
    return {
      timeText:['0','0','0','0','0','0'],
      topNum:["0%"],
    }
  },
  beforeMount:function(){
    var set$ = this.$set
    var timeText = this.timeText
    var topNum = this.topNum
    setInterval(function(){
      var time = new Date()
      var h = time.getHours().toString()
      var m = time.getMinutes().toString()
      var s = time.getSeconds().toString()
      var timeArr = [h,m,s]
      timeArr.forEach((str,i)=>{
        if (str!==timeText[i]) {
          if (parseInt(str)<10) {
            set$(timeText,2*i,'0')
            set$(timeText,2*i+1,str)
          }else {
            set$(timeText,2*i,str[0])
            set$(timeText,2*i+1,str[1])
          }
        }   
      })
      // console.log();
      set$(topNum,0,'-'+timeText[5]+'00%')
    },1000)
  }
}
</script>

<style lang="less">
@counterHeight:60px;
@blockWidth:40px;
.count{
  
  position: relative;
  overflow: hidden;
  display: inline-block;
  margin: 20px auto;
  font-weight: 900;
  height: @counterHeight;
  -webkit-mask-image:linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, #000000 5%, #000000 65%, rgba(0, 0, 0, 0) 100%);
}
.num{
  display: inline-block;
  font-family: "Noto Sans",sans-serif;
  color: #FFF;
  font-size: @counterHeight;
  line-height: @counterHeight;
}
.second{
  position: absolute;
  left: 0;
  width: 50%;
  transition: 0.2s all
}
.numContainer{
  display: inline-block;
  float: left;
  position: relative;
  height: 60px;
  overflow: hidden;
  width: @blockWidth;
}
.numContainer:nth-child(2n){
  width: @blockWidth*2;
  text-align: left;
}
.numContainer:nth-child(2n)::after{
  width: @blockWidth;
  position: absolute;
  right: 0;
  top: 0;
  color:#FFf;
  bottom: 0;
  display: inline-flex;
  font-size: @counterHeight;
  justify-content: center;
  align-items: center;
  content: ":"
}
.numContainer:last-child::after{
  display:none;
  width:@blockWidth;
}
</style>



