<template>
  <div >
    <mu-drawer :open="open" :docked="docked" @close="toggle()" :width="'77.777%'" class="drawer">
      <mu-list @itemClick="handlleClick">
        <div class="headArea" @click="handleShowUserInfo">
          <mu-avatar slot="left" icon="face":size="100" :iconSize="100"/>
          <mu-list class="badge">
            <mu-list-item title="Username">
              <mu-icon slot="right" value="arrow_drop_down"/>
            </mu-list-item>
          </mu-list>
        </div>

        <mu-list-item  v-for ="(key,val) in menu" :title="key" class="menuItem">
          <mu-icon :value ="val" slot="left"/>
        </mu-list-item>
        <mu-list-item v-if="docked" @click.native="open = false" title="Close"/>
      </mu-list>
    </mu-drawer>
  </div>
</template>

<script>
export default {
  props:['open','docked','toggle'],
  methods:{
    handlleClick(e){
      console.log(e.title+" was clicked");
      this.toggle()
    },
    handleShowUserInfo(e){
      console.log(1);
    }
  },
  data:function(){
    return {
      menu:{
        face:'个人',
        book:'草稿箱',
        settings:"设置",
        fullscreen:"扫一扫",
        brightness_2:"夜间",
        delete:"清除缓存",


      }
    }
  }

}
</script>

<style lang="css" scoped>
.badge{
  left: 0;
  bottom: 0;
  width: 100%;
  position: absolute;
  height: 25%;
  padding: 2.5%;
  text-align: left;
  flex-direction: column;
}
.headArea{
  height: 30%;
  background: #808080;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.drawer{
  display: flex;
  height: 100%;
  background: #C5C7C5
}
.menuItem{
  padding: 0 10px;
  text-align: left;
  width: 100%;
  flex-grow: 1;
  margin: 10px;
}
.menuItem:nth-child(4n){
  border-bottom: 1px solid #FFF
}
</style>



