<template lang="html">
  <div  v-if="search=='blog'" class ="columnContainer" @mouseover="handleHover" @mouseout="handleLoad">
    <div class="navBar" >
      <div :class="classList[key]" v-for="(name,key) in columnList"><i :class="iconList[key]"></i></div>
    </div>
    <div class="postContainer" v-if="act==key" v-for="(name,key) in columnList">
      <router-link :to="'/post/'+name+'/'+post.title" class="postLink" v-for="(post,key) in list[name]">
        <div class="linkText">
          <div class="linkTextTitle">
            {{post.title}}
          </div>
        </div>
        <div class="hiddenText">
          {{post.createdTime}}
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  props: ['list', 'search'],
  data: function() {
    return {
      threod:false,
      act:0,
      columnList:['listen','balabala','read','markdown'],
      iconList:["fa fa-music","fa fa-microphone","fa fa-book","fa fa-pencil"],
      classList:["blogcolumn active","blogcolumn deactive","blogcolumn deactive","blogcolumn deactive"],
      itv:'',
    }
  },
  methods: {
    handleHover: function(e) {
      clearInterval(this.itv)
      if (e.target.className.indexOf("blogcolumn")>=0) {
        var father = e.path[1]
        var that = this
        Array.from(father.children).forEach((t,i)=>{
          if (father.children[i]===e.target) {
            that.act = i
          }
          t.className="blogcolumn"
        })
        e.target.className="blogcolumn active"
      }
    },
    handleLoad:function(){
      var el = document.getElementsByClassName('navBar')
      if (!el.length) {
        return
      }else {
        var cList = Array.from(el[0].children)
      }
      var that = this
      if (this.itv) {
        clearInterval(this.itv)
      }
      this.itv = setInterval(function(
      ){
        that.act++
        if (that.act===cList.length) {
          that.act=0
        }      
        cList.forEach((t,i)=>{
          if (t!=cList[that.act]) {
            t.className='blogcolumn deactive'
          }else {
            t.className='blogcolumn active'
          }
        })
      },2000)
    }
  },
  updated:function functionName() {
    this.handleLoad()
  }
}
</script>
<style lang="less" scoped>
a{
  text-decoration: none;
  color:#FFF;
  text-align: center;
}
.columnContainer{
  display: flex;
  width: 500px;
  flex-wrap: wrap;
  height: 600px;
  .postContainer{
    width: 100%;
    background: #5C7590;
    height: 500px;
    overflow: scroll;
    display: flex;
    flex-wrap: wrap;
    .postLink{
      transition: all 0.5s;
      position: relative;
      width: 250px;
      height: 250px;
      box-sizing: border-box;
      border: 1px solid #FFF;
      z-index: 3;
      background-image: url(../assets/logo.png);
      background-size: 100% 100%;
      background-position: center;
      background-repeat: no-repeat;
      overflow: hidden;
      .linkText{
        position: absolute;
        bottom: 0;
        height: 25px;
        width: 100%;
        padding-top: 6.5%;
        background: rgba(0, 0, 0, 0.67);
        transition: all 0.5s; 
        z-index: 0;
        .linkTextTitle{
          position: absolute;
          width: 50%;
          left: 50%;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          margin-left: -25%;
          background: #FFFFFF;
          color: #4A4A4A;
          top: -50%;
          border: 1px solid #8C8C8C;
        }
      }
      .hiddenText{
        position: absolute;
        bottom: -25px;
        left: 0;
        right: 0;
        height: 25px;
        background: rgba(0, 0, 0, 0.8);
        z-index: -1;
        transition: all 0.5s;
      }
    }
    .postLink:hover{
      background-size: 110% 110%;
      .linkText{
        bottom: 25px;
      }
      .hiddenText{
        background: rgba(0, 0, 0, 0.8);
        bottom: 0;
      }
    }
    .postLink:nth-child(3n){
      width: 500px;
      height: 500px;
    }
  }
  .navBar{
    width: 100%;
    height: 2rem;
    display: flex;
    background: #EE1361;
    .active{
      background-color: rgba(255, 255, 255, 1);
    }
    .blogcolumn{
      font-size:15px;
      transition: all 0.6s;
      width: 25%;
    }
  }
}
</style>



