import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Post from '@/components/Post'
import wb from '@/components/WB.vue'
import search from '@/components/Search.vue'
import Game from '@/components/Game.vue'
import Count from '@/components/count.vue'
Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello,
    },
    {
      path: '/post/:column/:id',
      name: 'post',
      component:Post,
    },
    {
      path: '/wb/',
      name :'wb',
      component:wb,
      // children:[
      //   {path:'search',
      //   component:search}
      // ]
    },
    {
      path:'/wb/:column',
      name:'search',
      component:search
    },
    {
      path:'/game',
      name:'game',
      component:Game
    },
    {
      path:'/count',
      name:'count',
      component:Count
    }
  ]
})



